.navbar-m{
    position: fixed;
    right: 20px;
    display: flex;
    align-items: center;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    
}
.navbar-m i{
    color: white;
    /* z-index: 2; */
}
.navbar-m li{
    width: 50px;
    height: 50px;
    background-color: var(--blue);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1em 0;
    cursor: pointer;
}
.navbar-m a{
    width: 50px;
    height: 50px;
    /* background-color: var(--blue); */
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1em 0;
    cursor: pointer;
}
.navbar-m li:hover{
    background-color: var(--orange);
}
.navbar-m li a:hover{
    background-color: var(--orange);
}
.navbar-m li h1{
    display: flex;
    align-items: center;
    
    position: fixed;
    right: 0;
    opacity: 0;
    height: 50px;
    color: white;
    line-height: 50px;
    font-weight: 600 !important;
    text-transform: uppercase;
    transition: all .3s;
    border-radius: 30px;
    background-color: var(--orange);
}
h1 i {
    margin-left: 25px;
}
.navbar-m li h1:hover{

    opacity: 1;

    padding: 0 18px  0 25px;
    transition: all .3s;
    text-align: center;
    border: 30px;
    
}
.nav-active{

    background-color: var(--orange);

}
@media screen and (max-width : 1000px)  {
    .navbar-m{
        position: fixed;
        left: 0;
        bottom: 0;
        right: auto;
        top: auto;
        display: flex;
        width: 100%;
        align-items: center;
        transform: none;
        padding: 10px 0;

        z-index: 10;
        background-color: var(--blue);
        
    }
    .navbar-m ul {
        display: inline-flex;
        width: 100%;
        justify-content: space-evenly;
    }
    .navbar-m li{
        
        margin: 0 1em;
       
    }
    .navbar-m li h1{
        display: none;
    }
    .navbar-m a{
        background-color: var(--secondaryBlue);
    }
}